.userPhoto {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 10px;
}
.selectedPage {
    font-weight: bold;
    cursor: pointer;
    margin: 5px;
}

.paginator {
    margin: 10px;
}

.pageNumber {
    cursor: pointer;
    margin: 5px;
    padding: 2px;
    border: 1px solid pink;
}
.pageNumber .selectedPage{
    font-weight: bold;
    border: 2px solid darkblue;
}
p:hover {
    cursor: pointer;
}